import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../components/SearchBox.vue';
import Details from '../components/WordDetails.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Login,
  },
  {
    path: '/:language/:word',
    name: 'details',
    component: Details,
  },
  { path: '*', redirect: '/' },
];

const router = new VueRouter({
  routes,
  mode: 'history',
});

export default router;
